}
html {
  scroll-behavior: smooth;
  color-scheme: light;
  color: #0a0a0a;
  scrollbar-gutter: stable;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  overflow-x: hidden;
  background-color: #ffffff;
  color: #0a0a0a;
}
.hero {
  height: 100vh;
  width: 100vw;
  position: relative;
}
.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('../../public/carevia1.png') center/cover no-repeat;
  filter: blur(5px);
}
